
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'

import MyHead from '../components/head';
import house from '../lib/house.js';

export default {
components: {MyHead, Group, XInput, Cell, PopupRadio, XSwitch, XTextarea, XButton, Box, XAddress},
data() {
return {
title: null,//标题
money: null,//房租
type: 'A',//A:整租，B:单间
floor: null,//楼层
decation: '精装修',//装修程度
elevator: 1,//是否电梯
roomSize: null,
descr: null,
roomNo: null,
concatPhone:null,//联系号码
addressData: ChinaAddressV4Data,
showAddress: false,
address: []
};
},
methods: {
back() {
this.$router.go(-1);
},
addHouse() {
var data = {
title: this.title,
money: this.money || 0,
type: this.type,
floor: this.floor,
decation: this.decation,
elevator: this.elevator,
roomSize: this.roomSize,
descr: this.descr,
address: this.address,
roomNo:this.roomNo,
concatPhone:this.concatPhone,
deployTime:new Date().getTime()
};

var devices_array = [];
this.devices.forEach(dev => {
if (dev.has) {
devices_array.push(dev.key);
}
});
data.devices = devices_array;
var cur = this;
var errorMsg = null;
if(!data.title){
errorMsg = '标题不能为空';
}else if(data.money < 0){
errorMsg = '月租费用不能小于0';
}else if(data.address.length < 0){
errorMsg = '请选择地址';
this.showAddress = true;
}else if(data.devices.length < 0){
errorMsg = '总不至于什么设备都没有吧!';
}
if(errorMsg){
this.$vux.toast.text(errorMsg);
return;
}
house.addHouse(data, res => {
if (res.code == 0) {
this.$vux.toast.show({
text: '新增成功!',
onHide() {
cur.$router.push({
name: 'myhouse'
})
}
})
}
});
}
},
computed: {
rentHouseTypes() {
return this.$store.state.rentHouseTypes;
},
devices() {
return this.$store.state.devices;
}
}
}



